import { useMemo } from 'react';

export const useSortedBook = (books, sort) => {
  //  useMemo:
  // : () => { ... }  ,    .
  //  : [selectedSort, books]   ,      .
  const sortedBook = useMemo(() => {
    // ,            ,    .
    // useMemo              selectedSort  books.
    console.log('working');
    if (sort) {
      //  selectedSort  ,         (name  author)   localeCompare.
      return [...books].sort((a, b) => a[sort].localeCompare(b[sort])); // a     localeCompare    books      (name  author)
    }
    return books; //  selectedSort ,    books.
  }, [sort, books]);

  return sortedBook;
};

export const useBooks = (books, sort, query) => {
    const sortedBook = useSortedBook(books, sort);
    const sortedAndSearchedBook = useMemo(() => {
        //       sortedBook, ,     (   )  searchQuery (    ).
        // useMemo              searchQuery  sortedBook.
        return sortedBook.filter(book =>
          book.name.toLowerCase().includes(query)
        );
      }, [query, sortedBook]);
    return sortedAndSearchedBook;
}
